

export default {
  name: 'AgentSearch',

  props: [
    'value',
    'agent'
  ],

  data() {
    return {
      search: ''
    }
  },

  created() {
    if (this.agent) {
      this.search = this.agent
    }
  },

  methods: {
    async querySearchAsync(search, cb) {
      try {
        let data = await this.$store.dispatch('subagent/search', search)

        cb(data.map(d => {
          return {
            id: d.id,
            value: d.username,
            username: d.username
          }
        }))
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }
    },

    clear() {
      this.$emit('input', null)
    },

    handleSelect(item) {
      this.$emit('input', item.id)
      this.$emit('username', item.username)
    }
  }
}


