import _ from 'lodash'

import Paginate from '../../mixins/paginate'

import AgentSearch from '../../components/agent-search'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Paginate
  ],

  components: {
    AgentSearch
  },

  data() {
    return {
      action: null,
      list: {
        data: [],
        recordsFiltered: 0
      },
      player: {
        id: null,
        account_no: null,
        username: '',
        status: 1
      },
      rules: {
        username: [
          {
            required: true,
            message: 'Please input username',
            trigger: 'blur'
          }
        ],
        agent_id: [
          {
            required: true,
            message: 'The agent/sub-agent field is required.',
            trigger: 'blur'
          },
          {
            required: true,
            message: 'The agent/sub-agent field is required.',
            trigger: 'change'
          }
        ],
      },
      showDialog: false,
      isLoading: true,
      isFetching: false,
      isSaving: false,
      route,
    }
  },

  computed: {
    dialogTitle() {
      return this.action == 'add' ?
        'Add new player' : 'Edit player'
    },

    tableHeight() {
      return window.innerHeight - 250
    },
  },

  mounted() {
    this.fetch()
  },

  methods: {
    handleClose(done) {
      this.action = null
      this.player = {
        id: null,
        account_no: null,
        username: '',
        status: 1
      }

      if (typeof done == 'function') {
        done()
      }
    },

    openDialog(action) {
      this.action = action
      this.showDialog = true
    },

    closeDialog() {
      this.handleClose()
      this.showDialog = false
    },

    async fetch() {
      this.isLoading = true

      try {
        let params = {
          start: (this.currPage - 1) * this.currPageSize,
          length: this.currPageSize,

          // Global search
          'search[value]': this.search,

          // Agent
          'columns[0][data]': 'full_name',
          'columns[0][name]': 'full_name',
          'columns[0][searchable]': true,
          'columns[0][orderable]': true,
          'columns[0][search][value]': '',
          'columns[0][search][regex]': true,

          // Account #
          'columns[1][data]': 'account_no',
          'columns[1][name]': 'account_no',
          'columns[1][searchable]': true,
          'columns[1][orderable]': true,
          'columns[1][search][value]': '',
          'columns[1][search][regex]': true,

          // Player
          'columns[2][data]': 'username',
          'columns[2][name]': 'username',
          'columns[2][searchable]': true,
          'columns[2][orderable]': true,
          'columns[2][search][value]': '',
          'columns[2][search][regex]': true,

          // Contact #
          'columns[3][data]': 'contact',
          'columns[3][name]': 'contact',
          'columns[3][searchable]': true,
          'columns[3][orderable]': true,
          'columns[3][search][value]': '',
          'columns[3][search][regex]': true,
        }
        this.list = await this.$store.dispatch('player/list', params)
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async remove(id) {
      try {
        await this.$store.dispatch('player/remove', id)

        this.fetch()
        this.$message({
          type: 'success',
          message: 'Delete Successful'
        })
      } catch(e) {
        this.$message({
          type: 'error',
          message: 'Unable to remove.'
        })
      }
    },

    async edit(id) {
      this.isLoading = true

      try {
        this.player = await this.$store.dispatch('player/get', id)
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
      this.openDialog('edit')
    },

    async save() {
      if (this.isSaving) return

      this.$refs.form.validate(async valid => {
        if (!valid || this.isSaving) {
          return
        }

        this.isSaving = true

        try {
          delete this.player.account_no
          delete this.player.pa_id

          await this.$store.dispatch('player/save', this.player)
          let message = this.action == 'add' ?
            'Add Successful' : 'Update Successful'

          this.fetch()
          this.closeDialog()
          this.$message({
            type: 'success',
            message
          })
        } catch(e) {
          this.$message({
            type: 'error',
            message: e.message
          })
        }

        this.isSaving = false
      })
    }
  }
}



